.text-center {
  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #111111;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  height: 6rem;
}

.logo__desktop {
  display: none;
}
@media (min-width: 768px) {
  .logo__desktop {
    display: block;
    height: 35rem;
  }
}
.logo__movil {
  height: auto;
  width: 11rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .logo__movil {
    display: none;
  }
}

.navegacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion {
    margin-top: 2.5rem;
    height: 6rem;
    width: 100%;
    justify-content: center;
  }
}
.navegacion__links {
  display: flex;
  align-items: center;
}

.buscador__input {
  display: none;
  margin-top: 1.5rem;
  border-radius: 17px;
  min-width: 20rem;
  max-width: 40rem;
  height: 4rem;
  background-color: #f5f8fa;
  box-shadow: inset 0 0 0 1px #e1e8ed;
  border: 0;
  padding: 1rem;
}
@media (min-width: 768px) {
  .buscador__input {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1024px) {
  .buscador__input {
    display: flex;
    justify-content: space-between;
    height: 3rem;
    margin-top: 1.5rem;
    min-width: 30rem;
    max-width: 40rem;
  }
}
.buscador__lupa {
  border: none;
  background-color: transparent;
  width: 3rem;
  position: relative;
}
@media (min-width: 768px) {
  .buscador__lupa {
    display: none;
  }
}

#search-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.navegacion__bolsa {
  width: 3rem;
}

.catalogo {
  color: #111111;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .catalogo {
    font-size: 2rem;
    padding-right: 2rem;
  }
}

.descuento {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
  background-color: #f5f5f5;
}

.banner {
  display: flex;
  justify-content: center;
  max-height: 20rem;
  height: 15rem;
}
@media (min-width: 1024px) {
  .banner {
    min-height: 25rem;
    max-height: 27rem;
  }
}
.banner__img {
  background-image: url("../img/banner_logo.avif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
}

.filtro {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .filtro {
    justify-content: flex-end;
    margin-right: 5rem;
    height: 3rem;
  }
}

.opciones {
  margin: 1.5rem 1rem;
  height: 3rem;
}

.cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 2.5rem;
  gap: 1rem;
}
@media (min-width: 768px) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card__img {
  width: 23rem;
  height: 20rem;
}
@media (min-width: 768px) {
  .card__img {
    margin-top: 2rem;
  }
}
@media (min-width: 1024px) {
  .card__img {
    height: 45rem;
    width: 45rem;
  }
}

.footer {
  background-color: #282b30;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.footer__logo {
  height: 15rem;
  width: 300rem;
}
@media (min-width: 768px) {
  .footer__logo {
    margin-left: 12rem;
    width: 25rem;
    height: 13rem;
  }
}
@media (min-width: 1024px) {
  .footer__logo {
    margin-left: 38rem;
    width: 50rem;
    height: 20rem;
  }
}
.footer__icons {
  width: 35rem;
  margin: 1rem;
}
@media (min-width: 768px) {
  .footer__icons {
    margin-top: 2.5rem;
    margin-right: 3rem;
    width: 3.5rem;
  }
}
@media (min-width: 1024px) {
  .footer__icons {
    margin-top: 2.5rem;
    margin-right: 10rem;
    width: 4rem;
  }
}
.footer__text {
  color: #ffffff;
  position: absolute;
  bottom: -2.5rem;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  white-space: nowrap;
  margin-bottom: 1rem;
}
.footer__text p {
  font-size: 1.5rem;
  margin: 2rem 0;
}

.product {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .product {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .product {
    margin: 0 20rem;
  }
}
.product__title h2 {
  font-size: 2.5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .product__title h2 {
    font-size: 3rem;
    margin-top: 6rem;
  }
}
@media (min-width: 1024px) {
  .product__title {
    margin-top: 13rem;
  }
  .product__title h2 {
    font-size: 4rem;
  }
}
.product__price {
  padding: 2rem;
}
.product__price h3 {
  font-size: 2.2rem;
  margin: 0;
}
@media (min-width: 768px) {
  .product__price h3 {
    margin: 3rem;
    font-size: 4rem;
  }
}
.product__text-desktop {
  display: none;
}

.principal__img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50rem;
  height: 40rem;
  margin: 2rem 1rem;
  padding: 0 3rem;
}
@media (min-width: 1024px) {
  .principal__img {
    width: 70rem;
    height: 60rem;
  }
}

.wrapper__img {
  display: flex;
  gap: 1rem;
  width: 20rem;
  height: 10rem;
  margin: 2rem 4rem;
}
@media (min-width: 1024px) {
  .wrapper__img {
    width: 25rem;
    height: 10rem;
  }
}

.btn {
  background-color: #000000;
  border-radius: 28px;
  border: 1px solid #566963;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 15px;
  padding: 16px 76px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2b665e;
  margin-bottom: 1rem;
}
.btn:hover {
  background-color: #8f898f;
}
.btn:active {
  position: relative;
  top: 1px;
}

.share {
  display: flex;
  align-items: center;
  justify-content: center;
}
.share h4 {
  margin: 1rem;
}
@media (min-width: 768px) {
  .share {
    font-size: 2.5rem;
  }
}
.share__icons {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 5rem;
  width: 10rem;
  margin: 1rem;
}
@media (min-width: 768px) {
  .share__icons {
    margin: 2rem;
    height: 7rem;
    width: 13rem;
  }
}
.share .whatsapp {
  margin: 1rem;
}

.interes h3 {
  margin: 1rem;
}

.slider {
  padding: 3rem;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .slider {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
}
.slider__img {
  width: 30rem;
  height: 30rem;
}
@media (min-width: 768px) {
  .slider__img {
    height: 18rem;
  }
}
@media (min-width: 1024px) {
  .slider__img {
    width: 50rem;
    height: 40rem;
  }
}
.slider__info {
  text-align: center;
  font-size: 2rem;
}

@media (min-width: 768px) {
  .card {
    height: 27rem;
    width: 80rem;
  }
}
@media (min-width: 1024px) {
  .card {
    height: 52rem;
    width: 100rem;
  }
}

.hidden-card {
  display: none;
}
@media (min-width: 768px) {
  .hidden-card {
    display: block;
  }
}